import React, { useEffect } from "react";
import { Link, useHistory, useLocation } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

import { NavigationMenu, Footer } from "./";
import { logout as removeToken } from "../utils";
import {
  logout as logoutAction,
  login as loginAction,
} from "../Stores/User/actions";
import API from "../api";
import NotFound from "../pages/notFound";
import { withStyles } from "@material-ui/core/styles";
import { Avatar, Menu, MenuItem } from "@material-ui/core";
// active
const LayoutWrapper = (props) => {
  // const [user, setUser] = useState();
  const { children } = props;
  const { state } = useLocation();
  const history = useHistory();
  const { user } = useSelector((state) => state.userProviderReducer);
  const dispatch = useDispatch();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [anchorEl1, setAnchorEl1] = React.useState(null);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleAnnounceClick = (event) => {
    setAnchorEl1(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const handleCloses = () => {
    setAnchorEl1(null);
  };
  useEffect(() => {
    if (!user && !state) {
      API.get("/profile")
        .then((res) => {
          if (res.status === 200) {
            return dispatch(loginAction(res.data.user));
          }
        })
        .catch((err) => {
          console.log(err);
        });
    }
    // eslint-disable-next-line
  }, []);

  if (state === "404") return <NotFound />;

  const logout = () => {
    dispatch(logoutAction());
    removeToken().then(() => {
      history.push("/login");
    });
  };

  const handleToggleNav = (e) => {
    // e.preventDefault();
    const { classList } = document.body;
    classList.toggle("sidebar-enable");
    // 992 <= window.innerWidth ? classList.toggle("vertical-collpsed") : classList.remove("vertical-collpsed");
    return true;
  };

  const handleGoBack = () => {
    history.goBack();
  };

  const StyledMenu = withStyles({
    paper: {
      border: "1px solid #d3d4d5",
      width: "230px",
    },
  })((props) => (
    <Menu
      elevation={0}
      getContentAnchorEl={null}
      anchorOrigin={{
        vertical: "bottom",
        horizontal: "center",
      }}
      transformOrigin={{
        vertical: "top",
        horizontal: "center",
      }}
      {...props}
    />
  ));
  const StyledMenuItem = withStyles((theme) => ({
    root: {
      "&:focus": {
        backgroundColor: theme.palette.primary.main,
        "& .MuiListItemIcon-root, & .MuiListItemText-primary": {
          color: theme.palette.common.white,
        },
      },
    },
  }))(MenuItem);

  return (
    <>
      <div id="layout-wrapper">
        <header id="page-topbar">
          <div className="navbar-header">
            <div className="d-flex">
              {/* LOGO */}
              <div className="navbar-brand-box">
                <Link to="/" className="logo logo-dark">
                  <span className="logo-sm">
                    <img
                      src={`${process.env.PUBLIC_URL}/assets/images/readyforwork-logo.png`}
                      alt="logo light"
                      height={22}
                      style={{ transform: "translateX(-25%)" }}
                    />
                  </span>
                  <span className="logo-lg">
                    <img
                      src={`${process.env.PUBLIC_URL}/assets/images/readyforwork-logo.png`}
                      alt="logo light"
                      height={70}
                    />
                  </span>
                </Link>
                <Link to="/" className="logo logo-light">
                  <span className="logo-sm">
                    <img
                      src={`${process.env.PUBLIC_URL}/assets/images/readyforwork-logo.png`}
                      alt="logo light"
                      height={22}
                      style={{ transform: "translateX(-25%)" }}
                    />
                  </span>
                  <span className="logo-lg">
                    <img
                      src={`${process.env.PUBLIC_URL}/assets/images/readyforwork-logo.png`}
                      alt="logo light"
                      height={70}
                    />
                  </span>
                </Link>
              </div>
              <button
                onClick={handleToggleNav}
                type="button"
                className="btn btn-sm px-3 font-size-16 header-item waves-effect"
                id="vertical-menu-btn"
              >
                <i className="fa fa-fw fa-bars" />
              </button>
              <button
                onClick={handleGoBack}
                type="button"
                className="btn btn-sm px-3 font-size-16 header-item waves-effect"
                id="vertical-menu-btn"
              >
                Go Back
                {/* <i className="fa fa-fw fa-bars" /> */}
              </button>
            </div>
            <div className="d-flex">
              <div className="dropdown d-inline-block d-lg-none ms-2">
                <div
                  className="dropdown-menu dropdown-menu-lg dropdown-menu-end p-0"
                  aria-labelledby="page-header-search-dropdown"
                >
                  <form className="p-3">
                    <div className="form-group m-0">
                      <div className="input-group">
                        <input
                          type="text"
                          className="form-control"
                          placeholder="Search ..."
                          aria-label="Recipient's username"
                        />
                        <div className="input-group-append">
                          <button className="btn btn-primary" type="submit">
                            <i className="mdi mdi-magnify" />
                          </button>
                        </div>
                      </div>
                    </div>
                  </form>
                </div>
              </div>

              <div className="d-flex">
                <div
                  style={{ color: "black" }}
                  className="d-flex"
                  onClick={handleAnnounceClick}
                >
                  <button className="btn header-item waves-effect">
                    <i className="bx bxs-bell font-size-20" />
                  </button>
                </div>
                <div onClick={handleClick}>
                  <span
                    className="d-xl-inline-block ms-1"
                    key="t-henry"
                    style={{ cursor: "pointer" }}
                  >
                    {user ? `${user.firstName}` : "Guest"} &nbsp;
                    {user ? `${user.lastName}` : "Guest"}
                    <i className="mdi mdi-chevron-down d-none d-xl-inline-block"></i>
                  </span>
                  <button
                    type="button"
                    className="btn header-item waves-effect"
                  >
                    {user?.profile && (
                      <Avatar
                        src={`${process.env.REACT_APP_IMG_PUBLIC_URL}${user?.profile}`}
                      />
                    )}
                  </button>
                </div>
              </div>
              {/* <i className="bx bxs-bell"></i>
               <div> */}
              <StyledMenu
                id="customized-menu"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleClose}
              >
                <Link to="/create-profile">
                  <StyledMenuItem>
                    {" "}
                    <i className="bx bx-user font-size-18 align-middle me-3" />
                    Create Profile
                  </StyledMenuItem>
                </Link>

                <StyledMenuItem onClick={logout}>
                  {" "}
                  <i className="bx bx-power-off font-size-20 text-danger me-3"></i>
                  Logout
                </StyledMenuItem>
              </StyledMenu>

              <StyledMenu
                id="customized-menu"
                anchorEl={anchorEl1}
                keepMounted
                open={Boolean(anchorEl1)}
                onClose={handleCloses}
              >
                <StyledMenuItem>Announcements</StyledMenuItem>
              </StyledMenu>
            </div>
          </div>
        </header>
        {/* ========== Left Sidebar Start ========== */}
        <div style={{ overflowY: "auto" }} className="vertical-menu">
          <div data-simplebar className="h-100">
            {/*- Sidemenu */}
            <div id="sidebar-menu">
              {/* Left Menu Start */}
              <NavigationMenu onChange={handleToggleNav} />
            </div>
            {/* Sidebar */}
          </div>
        </div>
        {/* Left Sidebar End */}
        {/* ============================================================== */}
        {/* Start right Content here */}
        {/* ============================================================== */}
        <div className="main-content">
          <div className="page-content">{children && children}</div>
          <Footer />
        </div>
        {/* end main content*/}
      </div>
    </>
  );
};

export default LayoutWrapper;
